import { defineComponent } from "vue";
const componentName = "ILayout";
export default defineComponent({
  name: componentName,
  inheritAttrs: false,
  props: {
    /**
     * Display the layout on a vertical orientation
     * @type Boolean
     * @default false
     * @name vertical
     */
    vertical: {
      type: Boolean,
      default: false
    }
  },
  computed: {
    classes() {
      return { "-vertical": this.vertical };
    }
  }
});
