import { defineComponent } from "vue";
const componentName = "IContainer";
export default defineComponent({
  name: componentName,
  inheritAttrs: false,
  props: {
    /**
     * Display the container as fluid, always spanning 100% width
     * @type Boolean
     * @default false
     * @name fluid
     */
    fluid: {
      type: Boolean,
      default: false
    }
  },
  computed: {
    classes() {
      return {
        "-fluid": this.fluid
      };
    }
  }
});
